#modal {
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.4);
    position: fixed;
    z-index: 2;
}

.modal-inner {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.modal-box {
    width: 350px;
    background: #3c4245;
    padding: 20px;
    border-radius: 10px;
    outline: none;
    box-shadow: 0px 0px 7px 0px rgba(0,0,0,0.6);
}

.modal-title {
    text-align: center;
    padding: 10px 0px;
    font-size: 20px;
    color: #eee;
    margin-bottom: 20px;
    border-bottom: 1px solid #eee;
}

.modal-actions {
    display: flex;
    justify-content: space-between;
    margin-top: 24px;
}

.modal-btn-actions {
    display: flex;
    justify-content: center;
    border: none;
    padding: 10px 29px;
    border-radius: 20px;
    color: #3c4245;
    outline: none;
    cursor: pointer;
    align-items: center;
    width: 48%;

    &:last-child {
        background: #3c4245;
        color: #eee;
        border: 1px solid #eeee;
    }
}

label {
        position: absolute;
        font-size: 30px;
        color: red;
        right: 13px;
    }